'use client';

import type { FlightData } from '@/lib/types';
import { useEffect, useRef } from 'react';

interface FlightMapProps {
  flight: FlightData;
}

export function FlightMap({ flight }: FlightMapProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Set canvas size
    canvas.width = canvas.offsetWidth;
    canvas.height = canvas.offsetHeight;

    // Clear canvas
    ctx.fillStyle = 'rgba(15, 23, 42, 0.5)';
    ctx.fillRect(0, 0, canvas.width, canvas.height);

    // Draw simple USA map outline (simplified)
    ctx.strokeStyle = 'rgba(255, 255, 255, 0.2)';
    ctx.lineWidth = 2;

    // Convert lat/long to canvas coordinates
    // USA approximate bounds: lat 25-49, long -125 to -66
    const minLat = 25, maxLat = 49;
    const minLong = -125, maxLong = -66;

    const mapX = (long: number) => {
      return ((long - minLong) / (maxLong - minLong)) * canvas.width;
    };

    const mapY = (lat: number) => {
      return canvas.height - ((lat - minLat) / (maxLat - minLat)) * canvas.height;
    };

    // Draw flight path (simple line from origin to destination)
    // KSLC: 40.7884°N, 111.9778°W
    // KCVG: 39.0488°N, 84.6678°W
    const originLat = 40.7884;
    const originLong = -111.9778;
    const destLat = 39.0488;
    const destLong = -84.6678;

    ctx.strokeStyle = 'rgba(96, 165, 250, 0.4)';
    ctx.lineWidth = 3;
    ctx.setLineDash([10, 5]);
    ctx.beginPath();
    ctx.moveTo(mapX(originLong), mapY(originLat));
    ctx.lineTo(mapX(destLong), mapY(destLat));
    ctx.stroke();
    ctx.setLineDash([]);

    // Draw origin airport
    ctx.fillStyle = 'rgb(34, 197, 94)';
    ctx.beginPath();
    ctx.arc(mapX(originLong), mapY(originLat), 8, 0, Math.PI * 2);
    ctx.fill();

    // Draw destination airport
    ctx.fillStyle = 'rgb(239, 68, 68)';
    ctx.beginPath();
    ctx.arc(mapX(destLong), mapY(destLat), 8, 0, Math.PI * 2);
    ctx.fill();

    // Draw current position (airplane)
    const planeX = mapX(flight.longitude);
    const planeY = mapY(flight.latitude);

    // Draw airplane icon
    ctx.save();
    ctx.translate(planeX, planeY);
    ctx.rotate((flight.heading * Math.PI) / 180);

    // Airplane shape
    ctx.fillStyle = 'rgb(255, 255, 255)';
    ctx.beginPath();
    ctx.moveTo(0, -15);
    ctx.lineTo(-8, 5);
    ctx.lineTo(-3, 5);
    ctx.lineTo(-3, 10);
    ctx.lineTo(3, 10);
    ctx.lineTo(3, 5);
    ctx.lineTo(8, 5);
    ctx.closePath();
    ctx.fill();

    // Glow effect
    ctx.shadowColor = 'rgb(96, 165, 250)';
    ctx.shadowBlur = 20;
    ctx.fill();

    ctx.restore();

    // Draw labels
    ctx.font = '12px sans-serif';
    ctx.fillStyle = 'rgba(255, 255, 255, 0.8)';
    ctx.fillText('SLC', mapX(originLong) - 15, mapY(originLat) + 20);
    ctx.fillText('CVG', mapX(destLong) - 15, mapY(destLat) + 20);

    // Draw coordinates
    ctx.fillStyle = 'rgba(255, 255, 255, 0.6)';
    ctx.font = '10px monospace';
    ctx.fillText(
      `${flight.latitude.toFixed(4)}°N, ${Math.abs(flight.longitude).toFixed(4)}°W`,
      planeX + 20,
      planeY
    );
  }, [flight]);

  return (
    <div className="bg-white/10 backdrop-blur-md rounded-2xl p-6 border border-white/20">
      <h2 className="text-2xl font-bold mb-4">Flight Path</h2>
      <div className="relative w-full h-96 bg-slate-900/50 rounded-xl overflow-hidden">
        <canvas ref={canvasRef} className="w-full h-full" />
      </div>
    </div>
  );
}

